.container {
  display: flex;
  flex-direction: column;
  align-items: center;

  >* {
    margin-top: 30px;
  }
}

.grid {
  width: 602px;
  height: 602px;
  display: grid;
  grid-template-columns: 60px 60px 60px 60px 60px 60px 60px 60px 60px 60px;
  border: 1px solid black;
}


.squad {
  width: 60px;
  height: 60px;
  border: 1px solid black;
}

.actualSquad {
  background: blue;
}